$black: #000;
$grey: #eee;
$pink: #ffc0cb;

$text-size: 30px;

$day-size: 100px;
$day-gap: 1px;
$day-padding: 10px;
$day-move: $day-size + $day-gap;
$week:
  "mon" 0,
  "tue" $day-move,
  "wed" $day-move * 2,
  "thu" $day-move * 3,
  "fri" $day-move * 4,
  "sat" $day-move * 5,
  "sun" $day-move * 6;
